var token= ""
import helper from '../../common/utils.js';
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 0,
			slist: [],
			tlist: [],
			tabCurrentIndex: 0,
						navList: [
						]
			
		}
	},
	
	onShow(option){
		this.tabCurrentIndex = 0;
		this.currentId = 0;
		this.tlist=[]
		if(helper.catePid!="")
		{
			this.initTagPid(helper.catePid)
			this.currentId = helper.cateId
		}
		
		token = uni.getStorageSync("token")
		this.loadTypeData();
	},
	onHide() {
		helper.catePid=""
		helper.cateId=""
	},
	methods: {
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			console.log(item)
			uni.navigateTo({
				url: `/pages/product/product?id=${item}`
			})
		},
		async loadTypeData(){
			var _this = this
			uni.request({
			    url:this.$url+'/goods/type/findAll' ,//服务器端地址
			    data: {
					
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.navList = res.data.data
						if(_this.currentId===0)
						{
							_this.currentId = _this.navList[0].children[0].id
						}
						
						_this.loadGoods(_this.currentId)
			        }
			    }
			   
			});
		},
		async loadGoods(id)
		{
			var _this = this
			uni.request({
			    url:this.$url+'/goods/find/byType' ,//服务器端地址
			    data: {
					id:id,
					pageNum:"0",
					pageSize:"10"
			    },
			    method: 'POST',
			    header: {
			        'content-type': 'application/json',
					'token':token,
			    },
			    success: (res) => { 
					console.log(res.data)
			        if (res.data.code == 200) {
						_this.tlist = res.data.data.content
			        }
			    }
			   
			});
		},
		initTagPid(pid)
		{
			for(var i=0;i<this.navList.length;i++)
			{
				if(pid===this.navList[i].id)
				{
					this.tabCurrentIndex = i
				}
			}
		},
		changeTab(e) {
				this.tabCurrentIndex = e.target.current;
				},
				//顶部tab点击
				tabClick(index) {
					this.tlist=[]
					this.tabCurrentIndex = index;
					this.currentId = this.navList[index].children[0].id
					this.loadGoods(this.currentId)
				},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			this.tlist=[]
			this.currentId = item.id;
			this.loadGoods(this.currentId)
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid, tid){
			uni.navigateTo({
				url: `/pages/product/list?fid=${this.currentId}&sid=${sid}&tid=${tid}`
			})
		}
	}
}
